//@ts-ignore
if (ENVIRONMENT === "development" && typeof window !== "undefined") {
  window.useLogging = true;
}

export interface Tab extends chrome.tabs.Tab { }

export const getTabInfo = (): Promise<Tab> => {
  return new Promise((resolve, reject) => {
    chrome.runtime.sendMessage({ type: MSG_TAB_INFO_GET }, (resp: Tab) => {
      if (!resp) {
        reject(new Error("Error obtaining tab info."));
      } else {
        resolve(resp);
      }
    });
  });
};

export function getCurrentTabId() {
  return new Promise((resolve, reject) => {
    chrome.runtime.sendMessage({ type: MSG_TAB_ID_GET }, (resp) => {
      // {tabId: number}
      if (!resp) {
        reject(new Error("Error getting tab info"));
      } else {
        resolve(resp); // {tabId: number}
      }
    });
  });
}

export const recordBlockedAd = (tab: Tab): void => {
  try {
    // record the ad and then update the badge count
    chrome.runtime.sendMessage({
      type: MSG_RECORD_CONTENT_AD,
      tabId: tab.id,
      tabUrl: tab.url,
    });
  } catch (err) {
    console.error("RBAD: Error occurred while recording blocked ad. " + err);
  }
};

export const recordBlockedEasylistAd = (tab: Tab, msg: string): void => {
  try {
    // record the ad and then update the badge count
    chrome.runtime.sendMessage({
      type: msg,
      tabId: tab.id,
      tabUrl: tab.url,
    });
  } catch (err) {
    console.error("RBAD: Error occurred while recording blocked ad. " + err);
  }
};

export const isHidden = (element: HTMLElement): boolean => {
  return element?.style?.display.trim() === "none";
};

export const isEasyListEnabled = (): Promise<boolean> => {
  return new Promise((resolve, reject) => {
    chrome.runtime.sendMessage(
      {
        type: MSG_QUERY_FEATURE_FLAG,
        payload: { feature: FLAG_ENABLE_BLOCK_EASYLIST_ADS },
      },
      (response) => {
        if (!response) {
          reject(new Error("Error getting Easylist info"));
        } else {
          resolve(response.isEnabled);
        }
      }
    );
  });
};

export function isYoutubeCustomBlockingEnabled() {
  return new Promise((resolve, reject) => {
    chrome.runtime.sendMessage(
      {
        type: MSG_QUERY_FEATURE_FLAG,
        payload: { feature: FLAG_ENABLE_BLOCK_YOUTUBE_CUSTOM_ADS },
      },
      (response) => {
        if (!response) {
          reject(new Error("Error getting custom Youtube blocking info"));
        } else {
          resolve(response.isEnabled);
        }
      }
    );
  });
}

export const executeJavascript = (code: string): Promise<boolean> => {
  return new Promise((resolve) => {
    getCurrentTabId()
      .then(({ tabId }: any) => {
        chrome.runtime.sendMessage(
          {
            type: MSG_EXECUTE_INLINE_SCRIPT,
            tabId,
            source: code,
          },
          (response) => {
            if (response) {
              resolve(response);
            }
          }
        );
      })
      .catch((err) => {
        console.error(err);
      });
  });
};

export function downloadEasylistSelectors(
  tabInfo: Tab
): Promise<EasyListElement> {
  return new Promise((resolve, _reject) => {
    chrome.runtime.sendMessage(
      {
        type: MSG_GET_EASYLIST_SELECTORS,
        parameters: {
          type: "hide",
          domain: window.location.hostname,
          url: window.location.href,
          tabId: (tabInfo as any).tabId,
        },
      },
      (response: EasyListElement) => {
        resolve(response);
      }
    );
  });
}

function queryEasylistElements(tabInfo: Tab, url: string, type: string) {
  return new Promise((resolve, _reject) => {
    chrome.runtime.sendMessage(
      {
        type: MSG_QUERY_EASYLIST,
        parameters: {
          type,
          domain: window.location.hostname,
          url,
          tabId: (tabInfo as any).tabId,
        },
      },
      (response) => {
        // { what: "keep" } | { what: "remove" }
        resolve(response);
      }
    );
  });
}

export function checkIfEasylistApplicable(
  tabInfo: Tab,
  easylistItems: EasyListItems,
  easylistPrivacyItems: EasyListItems,
  visualDebugging = false,
  targetElements?: NodeListOf<HTMLElement> | HTMLElement[]
) {
  try {
    removeEasyListSelectors(
      tabInfo,
      easylistItems,
      MSG_RECORD_EASYLIST_AD,
      visualDebugging,
      targetElements
    );
    removeEasyListSelectors(
      tabInfo,
      easylistPrivacyItems,
      MSG_RECORD_EASYLIST_PRIVACY,
      visualDebugging,
      targetElements
    );
  } catch (error) {
    console.log("Error removing selectors", error);
  }
}

export function checkElements(tabInfo: Tab, targetElements?: NodeListOf<HTMLElement> | HTMLElement[]) {
  let elements: HTMLScriptElement[];

  if (targetElements) {
    // Filter targetElements to only include script elements
    elements = Array.from(targetElements).filter(
      (element): element is HTMLScriptElement => element.tagName.toLowerCase() === 'script'
    );
  } else {
    // Use all script elements in the document
    elements = Array.from(document.querySelectorAll("script"));
  }

  for (const element of elements) {
    queryEasylistElements(
      tabInfo,
      element.src,
      element.tagName.toLowerCase()
    ).then((response: any) => {
      if (response && response.what == "remove") {
        window.useLogging &&
          console.log("Removing script element: " + element.src);
        element.remove();
        recordBlockedEasylistAd(tabInfo, MSG_RECORD_EASYLIST_AD);
      }
    });
  }
}

export function monitorAds(tabInfo: Tab, indexScope: { shouldCheckAds: boolean; easylistItems: EasyListItems; easylistPrivacyItems: EasyListItems; visualDebugging: boolean }) {
  setInterval(() => {
    if (indexScope.shouldCheckAds) {
      window.useLogging && console.debug("MutationObserver MonitorAds: Checking for Easylist applicability.");
      checkIfEasylistApplicable(tabInfo, indexScope.easylistItems, indexScope.easylistPrivacyItems, indexScope.visualDebugging);
      indexScope.shouldCheckAds = false;
    }
  }, 250);
}

function markGDPRConsentAsRemoved() {
  try {
    localStorage.setItem(GDPR_REMOVAL_FLAG, 'true');
  } catch (e) {
    window.useLogging && console.debug("Error setting GDPR removal flag: ", e);
  }
}

function isGDPRConsentRemoved(): boolean {
  try {
    let isRemoved = localStorage.getItem(GDPR_REMOVAL_FLAG);
    return isRemoved === "true";
  } catch (e) {
    window.useLogging && console.debug("Error getting GDPR removal flag: ", e);
    return true; // do not insist on removing GDPR consent
  }
}

function removeGDPRConsent(clickables: any[]) {
  if (isGDPRConsentRemoved()) {
    return;
  }
  setTimeout(() => {
    clickables.forEach(function (entry) {
      let element = document.querySelector(entry.item);
      if (element) {
        window.useLogging && console.log("Clickable found: " + element);
        if (entry.action === "hide") {
          element.style.display = "none !important";
        }
        if (entry.action === "click") {
          element.click();
        }
      }
    });
    markGDPRConsentAsRemoved();
  }, 500);
}

/**
 * Hides the specified node by modifying its style properties.
 * @param {HTMLElement} node - The node to hide.
 * @param {boolean} isVisualDebugging - Flag to indicate if visual debugging is enabled.
 * @param {string} cssClass - The CSS class to apply if visual debugging is enabled.
 * @returns {boolean} - Returns true if the node was successfully hidden, false otherwise.
 */
export function hideNode(
  node: Element,
  isVisualDebugging: boolean,
  cssClass: string
): boolean {
  if ((node as any).m_isDeleted) {
    return false;
  }
  window.useLogging &&
    console.debug(
      "Removing element: " + node.tagName + " " + node.id + " " + node.className
    );
  let style = CSS_DISPLAY_NONE;
  if (isVisualDebugging) {
    style = cssClass;
  }
  node.classList.add(style);
  (node as any).m_isDeleted = true;
  return true;
}

export function removeEasyListSelectors(
  tabInfo: Tab,
  easylistItems: EasyListItems,
  message: string,
  visualDebugging = false,
  targetElements?: NodeListOf<HTMLElement> | HTMLElement[]
) {
  window.useLogging && console.debug("All elements to be removed: ", easylistItems.classes);
  const elementsToRemove = new Set<HTMLElement>();
  const exceptionRules = easylistItems.exception_rules;
  const exceptionElements = exceptionRules.map((s) => {
    return s && s.length > 0 && document.querySelectorAll(s);
  });

  let extended_hide = new Set(easylistItems.extended_hide);

  const ids = new Set(easylistItems.ids);
  const classes = new Set(easylistItems.classes);
  const tags = new Set(easylistItems.tags);
  const selectors = easylistItems.selectors;
  const clickables = easylistItems.clickables;

  const isException = (element: Element) =>
    exceptionElements.some(
      (nodeList) =>
        nodeList &&
        Array.from(nodeList).some(
          (node) =>
            node === element || JSON.stringify(node) === JSON.stringify(element)
        )
    );

  if (extended_hide.size > 0) {
    const parser = new SelectorParser();
    extended_hide.forEach((selector) => {
      try {
        const selectors = parser.parseSelector(selector);
        if (
          !selectors ||
          (Array.isArray(selectors) && selectors.length === 0)
        ) {
          return;
        }
        const elements = getElementsByRule(selectors);
        if (elements.length === 0) return;
        elements.forEach((element) => {
          if (isException(element)) {
            return;
          }
          elementsToRemove.add(element as HTMLElement);
          if ("title" in element) {
            element.title = "Blocked";
          }
        });
      } catch (e) {
        console.error("Error parsing rule: ", e);
      }
    });
  }

  // Use provided elements or fallback to all DOM elements
  const allElemsToBeRemoved = targetElements
    ? Array.from(targetElements)
    : Array.from(document.querySelectorAll("*") as NodeListOf<HTMLElement>);

  allElemsToBeRemoved.forEach((node: HTMLElement) => {
    if (isException(node)) {
      return;
    }

    if (node?.id && ids.has(`#${node.id}`)) {
      node.title = `Blocked (id): ${node.id}`;
      elementsToRemove.add(node);
    }
    if (node.tagName && tags.has(node.tagName)) {
      node.title = `Blocked (tag): ${node.tagName}`;
      elementsToRemove.add(node);
    }

    Array(node.classList).forEach((c) => {
      if (classes.has(`.${c}`)) {
        elementsToRemove.add(node);
        node.title = `Blocked (class): ${c}`;
      }
    });
  });

  easylistItems.specific_hide.forEach(function (item) {
    document.querySelectorAll(item).forEach(function (node) {
      if (
        hideNode(
          node,
          visualDebugging,
          CSS_DISPLAY_VISUAL_DEBUGGING_EL_SPECIFIC
        )
      ) {
        (node as any).title = "Blocked (specific): " + item;
        window.useLogging && console.log("Removing specific element: " + item);
      }
    });
  });

  removeGDPRConsent(clickables);

  if (selectors && selectors.length > 0) {
    document.querySelectorAll(selectors.join(",")).forEach(function (node) {
      (node as any).title = "Blocked (selector): ";
      elementsToRemove.add(node as HTMLElement);
    });
  }

  elementsToRemove.forEach(function (node) {
    window.useLogging && console.debug("Hiding element: ", node.className, node.id, node.tagName);
    if (
      hideNode(
        node,
        visualDebugging,
        CSS_DISPLAY_VISUAL_DEBUGGING_EL_GENERIC
      )
    ) {
      recordBlockedEasylistAd(tabInfo, message);
    }
  });
}


/**
 * Serialize the EasyList element for storage or transmission
 * @param rawEasylist The raw EasyList element to serialize
 * @param tabId The ID of the tab associated with the EasyList
 * @returns The serialized EasyList element
 */
export const serializeEasylist = (rawEasylist: EasyListElement, tabId: number): EasyListElement => {
  return {
    tabId: rawEasylist.tabId,
    easylist: {
      ids: new Set(rawEasylist.easylist.ids),
      classes: new Set(rawEasylist.easylist.classes),
      tags: new Set(rawEasylist.easylist.tags),
      selectors: rawEasylist.easylist.selectors,
      exception_rules: rawEasylist.easylist.exception_rules,
      specific_hide: rawEasylist.easylist.specific_hide,
      extended_hide: rawEasylist.easylist.extended_hide,
      clickables: rawEasylist.easylist.clickables
    },
    easylistprivacy: {
      ids: new Set(rawEasylist.easylistprivacy.ids),
      classes: new Set(rawEasylist.easylistprivacy.classes),
      tags: new Set(rawEasylist.easylistprivacy.tags),
      selectors: rawEasylist.easylistprivacy.selectors,
      exception_rules: rawEasylist.easylistprivacy.exception_rules,
      specific_hide: rawEasylist.easylistprivacy.specific_hide,
      extended_hide: rawEasylist.easylistprivacy.extended_hide,
      clickables: rawEasylist.easylistprivacy.clickables
    },
    visual_debugging: rawEasylist.visual_debugging
  }
}


/**
 * Check DOM elements for ads and remove them if they match easylist rules
 * if called with no targetElements, check the whole DOM
 */
export const checkDomElementsForAds = async (
  currentTab: Tab,
  globalEasylist: EasyListElement,
  heuristicCacheObj,
  protectionStatus: IsAdProtectionActiveResponse,
  targetElements?: NodeListOf<HTMLElement> | HTMLElement[],
) => {


  // if heuristics protection is active
  if (protectionStatus.isHeuristicsProtectionActive) {
    await hideHeuristicElements(currentTab, globalEasylist?.visual_debugging, heuristicCacheObj);
  }


  // if easylist protection is active
  if (protectionStatus.isEasylistProtectionActive) {
    // Remove <script> components based on easylist rules
    checkElements(currentTab, targetElements);

    // Remove elements based on easylist selectors
    checkIfEasylistApplicable(
      currentTab,
      globalEasylist.easylist,
      globalEasylist.easylistprivacy,
      globalEasylist.visual_debugging,
      targetElements
    );
  }

}


/**
 * DOM Observer to monitor changes in the DOM
 * and re-apply ad blocking rules as necessary
 */
export const DomObserver = (tab: Tab, easylist: EasyListElement, heuristicCacheObj, protectionStatus: IsAdProtectionActiveResponse) => {
  let debounceTimeoutId: ReturnType<typeof setTimeout> | null = null;
  let pendingNodes: Element[] = [];
  const DEBOUNCE_DELAY = 2000; // ms

  const isRelevantElement = (node: Node): node is Element => {
    /*
      New HTML elements (returns true)
      New text mutations when content is added (returns false)
      New comment nodes <!--> (returns false)
    */
    return node.nodeType === Node.ELEMENT_NODE;
  };

  const processBatch = async (nodes: Element[]) => {
    if (nodes.length === 0) return;

    // Convert Element[] to HTMLElement[] as required by checkIfEasylistApplicable
    const htmlElements = nodes.filter(node => node instanceof HTMLElement) as HTMLElement[];

    // check only the new elements
    checkDomElementsForAds(tab, easylist, heuristicCacheObj, protectionStatus, htmlElements);

  };

  const observer = new MutationObserver((mutations: MutationRecord[]) => {
    // Collect all new nodes in the batch
    for (const mutation of mutations) {
      for (const node of Array.from(mutation.addedNodes)) {
        if (isRelevantElement(node)) {
          pendingNodes.push(node);
        }
      }
    }

    // Clear existing timer and set a new one
    if (debounceTimeoutId) {
      clearTimeout(debounceTimeoutId);
    }

    debounceTimeoutId = setTimeout(() => {
      if (pendingNodes.length > 0) {
        // Process the entire batch at once
        processBatch([...pendingNodes]); // Copy array
        pendingNodes = []; // Clear the batch
      }
      debounceTimeoutId = null;
    }, DEBOUNCE_DELAY);
  });

  // Return control methods
  return {
    start: () => {
      observer.observe(document.body, {
        childList: true,
        subtree: true,
        attributes: false,
        characterData: false
      });
      useLogging && console.debug('DOM observer started for ad blocking');
    },
    stop: () => {
      if (debounceTimeoutId) {
        clearTimeout(debounceTimeoutId);
      }
      observer.disconnect();
      pendingNodes = [];
      useLogging && console.debug('DOM observer stopped');
    }
  };
};

export async function hideHeuristicElements(tabInfo: any, visualDebugging: boolean = false, heuristicCacheObj) {

  if (Object.keys(heuristicCacheObj.db).length === 0) { // If selectors are not loaded, we need to download them
    heuristicCacheObj.db = await downloadHeuristicsDB();
  }


  // If no rules are available, skip processing
  if (!heuristicCacheObj.db || Object.keys(heuristicCacheObj.db).length === 0) {
    console.debug("No heuristics rules available");
    return;
  }

  await processHeuristicsRules(heuristicCacheObj.db, tabInfo.url);
  await sendHeuristicsURLsToBlock();

  heuristicsElementsToRemove.forEach(function (node) {
    if (hideNode(
      node,
      visualDebugging,
      CSS_DISPLAY_VISUAL_DEBUGGING_HE
    )) {
      //TODO change to heuristics
      recordBlockedEasylistAd(tabInfo, MSG_RECORD_EASYLIST_AD);
    }
  });
}

// block chrome Topics API
// https://malwarebytes.atlassian.net/browse/BG-1266
export const removeBrowsingTopics = () => {
  if (!isMV3()) {
    injectJavascript(
      `if (!!document.browsingTopics) { delete Document.prototype.browsingTopics; }`
    );
  } else {
    injectJsFile('injection-topics.js', null);
  }
}


/**
 * YOUTUBE SPECIFIC
 */
export const youtubeSpecificRemovals = () => {

  const siteInfo = new URL(window.location.href);
  const host = siteInfo?.host?.toLocaleLowerCase();

  if (host === "youtube.com" || host.endsWith(".youtube.com")) {
    const afterDOMLoaded = () => {
      getTabInfo().then(async (tab: any) => {
        const shouldBlockAds: IsAdProtectionActiveResponse = await isAdsProtectionActive(tab.id, host);
        if (shouldBlockAds.isAdsProtectionActive === false || !(await isYoutubeCustomBlockingEnabled()) ) {
          return;
        }
        processYoutubeAds();
        setInterval(() => processYoutubeAds(), 1000);
      }).catch((err) => {
        console.debug(err);
      });

    }
    if (document.readyState === "loading") {
      document.addEventListener("DOMContentLoaded", afterDOMLoaded);
    } else {
      afterDOMLoaded();
    }
  }
};


/**
 * INSTART LOGIC
 * Remove instart ads by hiding their containers
 */
export const instartSpecificRemovals = () => {
  const siteInfo = new URL(window.location.href);
  const host = siteInfo?.host?.toLocaleLowerCase();
  const instartDomains = [
    "msn.com",
    "cnet.com",
    "gamespot.com",
    "ign.com",
    "slickdeals.net",
    "webmd.com",
    "sfgate.com",
    "chron.com",
    "metacritic.com",
    "pcmag.com",
    "ranker.com",
    "chicagotribune.com",
    "tvguide.com",
    "newsweek.com",
    "nasdaq.com",
    "sporcle.com",
    "medicinenet.com",
    "edmunds.com",
    "everydayhealth.com",
    "sportingnews.com",
    "metrolyrics.com",
    "boston.com",
    "thoughtcatalog.com",
    "emedicinehealth.com",
    "cafemom.com",
    "streetchopperweb.com",
    "seattlepi.com",
  ];
  // block intrusive ads delivered by invasive reverse proxy (Instart Logic)
  if (instartDomains.some((domain) => host.endsWith(domain))) {
    const handleNotWhitelisted = (property) => {
      const params = {
        isWhitelisted: false,
        property: property,
      };
      window.postMessage(
        {
          type: "isInstartWhitelistedResponse",
          parameters: JSON.stringify(params),
        },
        `${window.location.protocol}//${window.location.host}`
      );
    };

    window.addEventListener("message", async (event) => {
      if (!event.origin) {
        return;
      }
      if (event.origin !== window.location.origin) {
        return;
      }

      const originUrl = new URL(event.origin);
      const eventHost = originUrl.host;
      if (
        event.data.type === "isInstartWhitelisted" &&
        eventHost === window.location.host
      ) {
        let tab: Tab;
        try {
          tab = await getTabInfo();
        } catch (error) {
          console.debug("LIA: No valid tab found. ", error);
          return;
        }

        const params = JSON.parse(event.data.parameters);
        // checkIfInstartWhitelisted(params.href, params.prop);
        const shouldEnableInstart: IsAdProtectionActiveResponse = await isAdsProtectionActive(tab.id!, originUrl.toString());
        if (shouldEnableInstart.isAdsProtectionActive === false) {
          return;
        }

        console.debug("INS: handleNotWhitelisted", { params });
        handleNotWhitelisted(params.prop);
      }
    });

    const extId = chrome.runtime.id;
    injectJsFile("injection-instart.js", { extId: extId });
  }


};
