import {
    MSG_IS_AD_PROTECTION_ACTIVE,
    MSG_IS_ATTR_REPORTING_ACTIVE,
    MSG_RECORD_ARW,
    MSG_RECORD_PING_TRACKER_REMOVAL,
    MSG_IS_PING_TRACKER_REMOVER_ACTIVE,
} from "@/app/scripts/app-consts.js";
import { cleanUrl } from "@/utils/utils.js"
import { IsAdProtectionActiveResponse } from "@/utils/messaging/types";

export function isAdsProtectionActive(tabId: number, tabUrl: string): Promise<IsAdProtectionActiveResponse> {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({
            type: MSG_IS_AD_PROTECTION_ACTIVE,
            tabId,
            domain: cleanUrl(tabUrl),
        },
            (response) => {
                if (!response) {
                    reject(new Error("Error getting protection info"));
                } else {
                    resolve(response);
                }
            });
    });
}

