export function recordAttributionblock(tabId: number, tabUrl: string): Promise<void> {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({
            type: MSG_RECORD_ARW, tabId, tabUrl: cleanUrl(tabUrl),
        }, (resp) => {
            if (!resp.success) {
                const errMessage = "ARW: Error recording attribution block while sanitizing"
                console.error(errMessage);
                reject(new Error(errMessage));
            } else {
                console.log("ARW: Attribution block recorded for prop sanitization");
                resolve();
            }
        });
    })
}

export async function recordPingTrackerRemoval(tabId: number, tabUrl: string): Promise<void> {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({
            type: MSG_RECORD_PING_TRACKER_REMOVAL, tabId, tabUrl: cleanUrl(tabUrl),
        }, (resp) => {
            if (!resp.success) {
                const errMessage = "Error recording ping tracker removal"
                console.error(errMessage);
                reject(new Error(errMessage));
            } else {
                console.log("Ping tracker removal recorded");
                resolve();
            }
        });
    });
}

export function getArticle(word: string, locale: string): string {

    if (!locale.toLocaleLowerCase().startsWith('en')) return word;

    const vowels = ['a', 'e', 'i', 'o', 'u'];
    const firstLetter = word[0].toLowerCase();

    if (vowels.includes(firstLetter)) {
        return `an ${word}`;
    } else {
        return `a ${word}`;
    }
}